/*
font-family: 'Ubuntu', sans-serif; 300 400 500 700
*/


section {
    width : 100%;
    height: auto;
    float : left;
}

section#header {
    #top {
        background: #fff;
        height    : 130px;

        .logo {
            padding-top: 30px;
        }

        .navbar {
            float: right;

            ul {
                list-style: none;
                margin    : 0;

                li {
                    float         : left;
                    height        : 94px;
                    padding       : 20px;
                    padding-top   : 60px;
                    padding-bottom: 70px;

                    a {
                        color          : #ae1b1b;
                        text-decoration: none;
                        font-weight    : 500;
                        font-size      : 20px;

                    }

                    &:hover {
                        background: #ae1b1b;

                        a {
                            color: #fff;
                        }
                    }

                    &.active {
                        background: #ae1b1b;

                        a {
                            color: #fff;
                        }
                    }
                }
            }
        }
    }
}

section#slider {
    .slider {
        .item {
            img {
                height: 480px;
                width : 100%;

                @media (max-width: 1024px) {
                    height: 340px;
                }
            }
        }
    }
}

section#reference {
    padding: 40px 0;

    .reference {
        .content {
            padding-top: 30px 0;

            h2 {
                font-size  : 40px;
                font-weight: 100;

                @media (max-width: 1024px) {
                    font-size: 30px;
                }

                img {
                    margin: 0 50px;
                }
            }
        }
    }
}

section#delivery {
    .delivery {
        position                  : fixed;
        left                      : 0;
        top                       : 450px;
        z-index                   : 9999999999999;
        border-top-right-radius   : 12px;
        border-bottom-right-radius: 12px;

        &:hover {
            -webkit-box-shadow: 9px 7px 5px rgba(50, 50, 50, 0.77);
            -moz-box-shadow   : 9px 7px 5px rgba(50, 50, 50, 0.77);
            box-shadow        : 9px 7px 5px rgba(50, 50, 50, 0.77);
        }

        img {
            height: 250px;
            width : auto;
        }
    }
}