section#contact {
    background-image: url(../../img/bg-contact.png);
    height          : 500px;

    .contact {
        .bg-contact-1 {
            position    : absolute;
            padding-top : 250px;
            padding-left: 330px;

            img {
                width: 200px;
            }
        }

        .title {
            padding-top: 70px;
            text-align : center;

            img {
                width     : 6%;
                margin-top: -55px;
            }

            b {
                color    : #fff;
                font-size: 35px;

                span {
                    color: #972222;
                }
            }

        }

        .form {
            text-align : center;
            padding-top: 35px;
            margin     : 0 150px;

            input,
            textarea {
                background : transparent;
                border     : solid 1px #fff;
                color      : #fff;
                text-align : center;
                margin     : 0 0 10px;
                width      : 100%;
                line-height: 2;
                font-weight: 500;
            }

            textarea {
                text-align: left;
                padding   : 5px 20px 0;
            }

            button {
                float     : right;
                background: transparent;
                border    : solid 1px #fff;
                color     : #fff;
            }
        }
    }
}

section#footer {
    background: #972222;

    .adress {
        color         : #fff;
        padding       : 35px 0;
        text-transform: uppercase;

        b {
            font-size: 12px;

            @media (max-width: 1024px) {
                font-size: 10px;
            }
        }

        b:nth-child(1) {
            font-size: 14px
        }

        span {
            font-size: 10px;
        }
    }

    .logo-footer {
        text-align : center;
        padding-top: 60px;
    }

    .text {
        color         : #fff;
        text-transform: uppercase;
        text-align    : right;
        padding       : 70px 0;
        font-size     : 14px;
        line-height   : 2;
    }
}